import React, { Component } from "react"

import artbord_1 from "./../Assets/Images/Artboard – 1.jpg"
import artbord_2 from "./../Assets/Images/Artboard – 2.jpg"
import artbord_3 from "./../Assets/Images/Artboard – 3.jpg"
import artbord_4 from "./../Assets/Images/Artboard – 4.jpg"
import artbord_5 from "./../Assets/Images/Artboard – 5.jpg"
import artbord_6 from "./../Assets/Images/Artboard – 6.jpg"


class Work extends Component {
  render() {
    return (
      <div className="work">
        <h1 className="">My Recent Work</h1>
        <p className="text-dark">Here are a few past design projects I've worked on. Want to see more? <a href="mailto:dhialajili4@gmail.com">Email me</a>.</p>
        <ul className="grid-container" data-aos="fade-up">
          <li>
            <img src={artbord_1} alt="artbord-1"/>
          </li>
          <li>
            <img src={artbord_2} alt="artbord-2"/>
          </li>
          <li>
            <img src={artbord_3} alt="artbord-3"/>
          </li>
          <li>
            <img src={artbord_4} alt="artbord-4"/>
          </li>
          <li>
            <img src={artbord_5} alt="artbord-5"/>
          </li>
          <li>
            <img src={artbord_6} alt="artbord-6"/>
          </li>

        </ul>
      </div>
    )
  }
}

export default (Work);